.score {
	min-height: 40px;
	display: flex;
	align-items: center;
	justify-content: center;
}

.live {
	background-color: skyblue;
}

.finished {
	background-color: gold;
}

.pending {
	background-color: grey;
}

div.pairingRound.roundSelected {
	background-color: cyan;
}

div.pairingRound.nullRound, div.pairingRound.nullRound:hover > * {
	background-color: lightgrey;
	color: grey;
}

td.inner {
	padding: 0;
}

.subheader {
	display: grid;
	grid-template-columns: 1fr 1fr;
	padding: 20px 0;
}

.subheader > div {
	display: flex;
}

.subheader > div:nth-child(odd) {
	justify-content: flex-start;
}

.subheader > div:nth-child(even) {
	justify-content: flex-end;
}

.searchBar {
	display: flex;
	flex-direction: row;
	background-color: skyblue;
	border-radius: 10px;
	align-items: center;
	align-content: center;
	box-sizing: border-box;
}

.searchBar > * {
	height: 100%;
	padding: 0 5px;
}

.searchBar * {
	background-color: transparent;
}

.searchBar form {
	padding-bottom: 4px;
}

.searchBar div, .searchBar form, .searchBar input {
	box-sizing: border-box;
	height: 100%;
}

.searchBar input, .searchBar input:focus {
	border: none;
	padding: 0;
	margin: 0;
}