.th {
	cursor: pointer;
}

.round {
	min-width: 20px;
}

/* Standings */

th.headerCell {
	display: none;
}

@media only screen and (max-width: 700px) {
	th.headerCell {
		display: table-cell;
	}
	th.headerCell > div {
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	table > tbody > tr.tr:nth-child(n) {
		display: grid;
		grid-template-columns: 30.8% 68.1%;
		background-color: #f0f0f0;
	}
	tr.tr > th:nth-child(4n), tr.tr > th:nth-child(4n-1) {
		background-color: lightblue;
	}
}