.container {
	display: grid;
	grid-template-columns: 30.8% 68.1%;
	grid-template-rows: 68.1% 30.8%;
	column-gap: 20px;
	margin: 40px 0;
}

@media only screen and (max-width: 700px) {
	.container {
		grid-template-rows: unset;
		grid-template-columns: unset;
		grid-auto-flow: column;
		display: flex;
		flex-direction: column;
	}
}

.profilePicture {
	border: 5px solid black;
	grid-row: 1 / span 2;
	border-radius: 5px;
}

a.website {
	grid-column: 2 / span 1;
	width: 30.8%;
	min-width: 150px;
	border-radius: 10px;
	align-items: center;
	column-gap: 10px;
}

a.website > svg {
	justify-self: end;
}